.new {
  width: 100%;
  display: flex;

  .newContainer {
    flex: 6;
  }
    .top,
    .bottom {
      -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
      box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
      padding: 10px;
      margin: 20px;
      display: flex;

      h1 {
        color: lightgray;
        font-size: 20px;
      }
      
       form{
        width:100%;
       }
       .btnSolid {
        width: 150px;
        padding: 10px;
        border: none;
        background-color: teal;
        color: white;
        font-weight: bold;
        cursor: pointer;
        margin-top: 10px;
      }

      .left {
        flex: 1;
        text-align: center;

        img {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          object-fit: cover;
        }
        
      }

      .right {
        flex: 2;

        
          .formInput {
            width: 40%;

            label {
              display: flex;
              align-items: center;
              gap: 10px;

              .icon {
                cursor: pointer;
              }
            }

            input {
              width: 100%;
              padding: 5px;
              border: none;
              border-bottom: 1px solid gray;
            }
          

         
        }
      
    }
  }
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  justify-content: center;


label {
  width: 100px;
  margin-right: 10px;
  text-align: left;
}

input, select{
  width: 300px;
  margin-right: 10px;
  text-align: left;
  border: 1px solid gray;
}
}